/**
 * 有些情况需要换下 displayName
 * @author jero
 * @date 2016-08-17
 */

const MAP = {
  DialogWrap: 'Dialog',
  '[IMUI Input]': 'Input', // form
  '[IMUI Textarea]': 'Textarea'  // form
};

export function displayName(ReactElement) {
  const name = ReactElement.type.displayName ||
    ReactElement.type.name || // function name
    (typeof ReactElement.type === 'function' ? // function without a name, you should provide one
      'No Display Name' :
      ReactElement.type);

  return MAP[name] || name;
}
